.input_wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background-color: #403770;
  padding: 20px;
  opacity: 90%;
}

.bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000;
}

.modal {
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0px 0px 20px rgb(45, 45, 45);
}

.container {
  background-image: linear-gradient(to bottom right, #33adff, #7300e6);

  width: 100%;
  background-size: cover;
  background-position: center center;
  display: flex;
  align-items: center;
  justify-content: center;
  //filter: brightness(60%);

  .content {
    position: relative;
    margin-top: 7%;
    margin-bottom: 5%;
    background: #4c358e;
    border-radius: 20px;
    width: 50%;
    color: white;

    .font {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
      .title {
        text-align: center;
      }
    }
    .button {
      display: block;
      margin-left: auto;
      margin-right: auto;
      //       background-color: #6026ac;
      //       border-radius: 5%;
      //       border: none;
      //       color: white;
      //       cursor: pointer;

      //       padding: 15px 32px;

      //       margin-top: 5%;
      //       margin-bottom: 5%;
      --c: rgb(45, 28, 138);
      color: var(--c);
      font-size: 15px;
      border: 0.3em solid var(--c);
      border-radius: 0.5em;
      width: 12em;
      height: 3em;
      text-transform: uppercase;
      font-weight: bold;
      font-family: sans-serif;
      letter-spacing: 0.1em;

      margin-top: 1em;
      margin-bottom: 1em;
    }
  }
}
